.detail
{
    background: url(../img/bg.jpg) no-repeat;
    background-size: cover;
    height: 100vh;
    padding: 80px 20px;
    .inner 
    {
        .detail__content
        {
            height: 100%;
            position: relative;
            h1
            {
                font-size: 17px;
                margin: 20px 0;
            }
            h2
            {
                position: absolute;
                bottom: -25px;
                left: 0;
            }
        }
        .detail__btns
        {
            text-align: right;
            button
            {
                margin-right: 20px;
                font-size: 17px;
                :hover
                {
                    color: #e3ebfa;
                }
            }
        }
        .modal
        {
            text-align: right;
            
        }
    }
}